import React from "react";
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js'
import * as THREE from 'three'
import { RenderPass } from 'three/examples/jsm/postprocessing/RenderPass.js'
import { EffectComposer } from 'three/examples/jsm/postprocessing/EffectComposer.js'

// import { GlitchPass } from 'three/examples/jsm/postprocessing/GlitchPass.js'
// import { DotScreenPass } from 'three/examples/jsm/postprocessing/DotScreenPass.js'
// import { GlitchPass } from 'three/examples/jsm/postprocessing/GlitchPass.js'
// import { ShaderPass } from 'three/examples/jsm/postprocessing/ShaderPass.js'
// import { RGBShiftShader } from 'three/examples/jsm/shaders/RGBShiftShader.js'
// import { GammaCorrectionShader } from 'three/examples/jsm/shaders/GammaCorrectionShader.js'
// import { SMAAPass } from 'three/examples/jsm/postprocessing/SMAAPass.js'
// import { UnrealBloomPass } from 'three/examples/jsm/postprocessing/UnrealBloomPass.js'
import { TweenMax, Power1 } from "gsap";


import trippyVertexShader2 from './trippyVertexShader2.glsl'
import trippyFragmentShader2 from './trippyFragmentShader2.glsl'

import randomColor from 'randomcolor'


import {getRenderer,getScene1,getLight1,getCamera1,getCamera2,getControls1,getControls2,getEffectComposer,
  getPlaneRoundedCorners, getPlaneRoundedCornersTransparent,getAVAXLogo,getTrippyMesh,triangleGeometry} from "../functions"



const clock = new THREE.Clock()

class TrippyElement7 extends React.Component {

  constructor(props) {
    super(props)
    this.state={
      shouldStopAutoRotate:false
    }

    this.start = this.start.bind(this)
    this.stop = this.stop.bind(this)
    this.animate = this.animate.bind(this)
    this.onWindowResize = this.onWindowResize.bind(this)
    this.getSizes = this.getSizes.bind(this)
  }

  onWindowResize() {
    if(this.mount != null){
      this.camera.aspect =this.mount.clientWidth / this.mount.clientHeight
      this.camera.updateProjectionMatrix()
      this.camera2.aspect =this.mount.clientWidth / this.mount.clientHeight
      this.camera2.updateProjectionMatrix()
      this.renderer.setSize(this.mount.clientWidth, this.mount.clientHeight)
    }
  }

  getSizes(){
    const sizes = {
      width: this.mount.clientWidth,
      height: this.mount.clientHeight
    }
    window.addEventListener('resize', this.onWindowResize, false);
    return sizes
  }

  componentDidMount() {
    const sizes = this.getSizes()
    // Canvas
    // Scene 1
    const renderer = getRenderer(sizes)
    const canvas = renderer.domElement
    const scene = getScene1()

    const settings = {
      speed: 0.2,
      density: 1.5,
      strength: 0.2,
      frequency: 2.0,
      amplitude: 6.0,
      intensity: 7.0,
    };

    const noise = `
    // GLSL textureless classic 3D noise "cnoise",
    // with an RSL-style periodic variant "pnoise".
    // Author:  Stefan Gustavson (stefan.gustavson@liu.se)
    // Version: 2011-10-11
    //
    // Many thanks to Ian McEwan of Ashima Arts for the
    // ideas for permutation and gradient selection.
    //
    // Copyright (c) 2011 Stefan Gustavson. All rights reserved.
    // Distributed under the MIT license. See LICENSE file.
    // https://github.com/ashima/webgl-noise
    //
  
    vec3 mod289(vec3 x)
    {
      return x - floor(x * (1.0 / 289.0)) * 289.0;
    }
  
    vec4 mod289(vec4 x)
    {
      return x - floor(x * (1.0 / 289.0)) * 289.0;
    }
  
    vec4 permute(vec4 x)
    {
      return mod289(((x*34.0)+1.0)*x);
    }
  
    vec4 taylorInvSqrt(vec4 r)
    {
      return 1.79284291400159 - 0.85373472095314 * r;
    }
  
    vec3 fade(vec3 t) {
      return t*t*t*(t*(t*6.0-15.0)+10.0);
    }
  
    // Classic Perlin noise, periodic variant
    float pnoise(vec3 P, vec3 rep)
    {
      vec3 Pi0 = mod(floor(P), rep); // Integer part, modulo period
      vec3 Pi1 = mod(Pi0 + vec3(1.0), rep); // Integer part + 1, mod period
      Pi0 = mod289(Pi0);
      Pi1 = mod289(Pi1);
      vec3 Pf0 = fract(P); // Fractional part for interpolation
      vec3 Pf1 = Pf0 - vec3(1.0); // Fractional part - 1.0
      vec4 ix = vec4(Pi0.x, Pi1.x, Pi0.x, Pi1.x);
      vec4 iy = vec4(Pi0.yy, Pi1.yy);
      vec4 iz0 = Pi0.zzzz;
      vec4 iz1 = Pi1.zzzz;
  
      vec4 ixy = permute(permute(ix) + iy);
      vec4 ixy0 = permute(ixy + iz0);
      vec4 ixy1 = permute(ixy + iz1);
  
      vec4 gx0 = ixy0 * (1.0 / 7.0);
      vec4 gy0 = fract(floor(gx0) * (1.0 / 7.0)) - 0.5;
      gx0 = fract(gx0);
      vec4 gz0 = vec4(0.5) - abs(gx0) - abs(gy0);
      vec4 sz0 = step(gz0, vec4(0.0));
      gx0 -= sz0 * (step(0.0, gx0) - 0.5);
      gy0 -= sz0 * (step(0.0, gy0) - 0.5);
  
      vec4 gx1 = ixy1 * (1.0 / 7.0);
      vec4 gy1 = fract(floor(gx1) * (1.0 / 7.0)) - 0.5;
      gx1 = fract(gx1);
      vec4 gz1 = vec4(0.5) - abs(gx1) - abs(gy1);
      vec4 sz1 = step(gz1, vec4(0.0));
      gx1 -= sz1 * (step(0.0, gx1) - 0.5);
      gy1 -= sz1 * (step(0.0, gy1) - 0.5);
  
      vec3 g000 = vec3(gx0.x,gy0.x,gz0.x);
      vec3 g100 = vec3(gx0.y,gy0.y,gz0.y);
      vec3 g010 = vec3(gx0.z,gy0.z,gz0.z);
      vec3 g110 = vec3(gx0.w,gy0.w,gz0.w);
      vec3 g001 = vec3(gx1.x,gy1.x,gz1.x);
      vec3 g101 = vec3(gx1.y,gy1.y,gz1.y);
      vec3 g011 = vec3(gx1.z,gy1.z,gz1.z);
      vec3 g111 = vec3(gx1.w,gy1.w,gz1.w);
  
      vec4 norm0 = taylorInvSqrt(vec4(dot(g000, g000), dot(g010, g010), dot(g100, g100), dot(g110, g110)));
      g000 *= norm0.x;
      g010 *= norm0.y;
      g100 *= norm0.z;
      g110 *= norm0.w;
      vec4 norm1 = taylorInvSqrt(vec4(dot(g001, g001), dot(g011, g011), dot(g101, g101), dot(g111, g111)));
      g001 *= norm1.x;
      g011 *= norm1.y;
      g101 *= norm1.z;
      g111 *= norm1.w;
  
      float n000 = dot(g000, Pf0);
      float n100 = dot(g100, vec3(Pf1.x, Pf0.yz));
      float n010 = dot(g010, vec3(Pf0.x, Pf1.y, Pf0.z));
      float n110 = dot(g110, vec3(Pf1.xy, Pf0.z));
      float n001 = dot(g001, vec3(Pf0.xy, Pf1.z));
      float n101 = dot(g101, vec3(Pf1.x, Pf0.y, Pf1.z));
      float n011 = dot(g011, vec3(Pf0.x, Pf1.yz));
      float n111 = dot(g111, Pf1);
  
      vec3 fade_xyz = fade(Pf0);
      vec4 n_z = mix(vec4(n000, n100, n010, n110), vec4(n001, n101, n011, n111), fade_xyz.z);
      vec2 n_yz = mix(n_z.xy, n_z.zw, fade_xyz.y);
      float n_xyz = mix(n_yz.x, n_yz.y, fade_xyz.x);
      return 2.2 * n_xyz;
    }
  `;
  
  const rotation = `
    mat3 rotation3dY(float angle) {
      float s = sin(angle);
      float c = cos(angle);
  
      return mat3(
        c, 0.0, -s,
        0.0, 1.0, 0.0,
        s, 0.0, c
      );
    }
    
    vec3 rotateY(vec3 v, float angle) {
      return rotation3dY(angle) * v;
    }  
  `;
  
  const vertexShader = `  
    varying vec2 vUv;
    varying float vDistort;
    
    uniform float uTime;
    uniform float uSpeed;
    uniform float uNoiseDensity;
    uniform float uNoiseStrength;
    uniform float uFrequency;
    uniform float uAmplitude;
    
    ${noise}
    
    ${rotation}
    
    void main() {
      vUv = uv;
      
      float t = uTime * uSpeed;
      float distortion = pnoise((normal + t) * uNoiseDensity, vec3(10.0)) * uNoiseStrength;
  
      vec3 pos = position + (normal * distortion);
      float angle = sin(uv.y * uFrequency + t) * uAmplitude;
      pos = rotateY(pos, angle);    
      
      vDistort = distortion;
  
      gl_Position = projectionMatrix * modelViewMatrix * vec4(pos, 1.);
    }  
  `;
  
  const fragmentShader = `
    varying vec2 vUv;
    varying float vDistort;
    
    uniform float uTime;
    uniform float uIntensity;
    
    vec3 cosPalette(float t, vec3 a, vec3 b, vec3 c, vec3 d) {
      return a + b * cos(6.28318 * (c * t + d));
    }     
    
    void main() {
      float distort = vDistort * uIntensity;
      
      vec3 brightness = vec3(0.5, 0.5, 0.5);
      vec3 contrast = vec3(0.5, 0.5, 0.5);
      vec3 oscilation = vec3(1.0, 1.0, 1.0);
      vec3 phase = vec3(0.0, 0.1, 0.2);
    
      vec3 color = cosPalette(distort, brightness, contrast, oscilation, phase);
      
      gl_FragColor = vec4(color, 1.0);
    }  
  `;

    const geometry = new THREE.IcosahedronBufferGeometry(1, 64);
    const material = new THREE.ShaderMaterial({
      vertexShader,
      fragmentShader,
      uniforms: {
        uTime: { value: 0 },
        uSpeed: { value: settings.speed },
        uNoiseDensity: { value: settings.density },
        uNoiseStrength: { value: settings.strength },
        uFrequency: { value: settings.frequency },
        uAmplitude: { value: settings.amplitude },
        uIntensity: { value: settings.intensity },
      },
      // wireframe: true,
    });

 

    let mesh = new THREE.Mesh(geometry, material);

    mesh.material.uniforms.uSpeed.value = settings.speed;    
    mesh.material.uniforms.uNoiseDensity.value = settings.density;
    mesh.material.uniforms.uNoiseStrength.value = settings.strength;
    mesh.material.uniforms.uFrequency.value = settings.frequency;
    mesh.material.uniforms.uAmplitude.value = settings.amplitude;
    mesh.material.uniforms.uIntensity.value = settings.intensity;

    scene.add(mesh);




    // const trippyMesh =  getTrippyMesh(trippyVertexShader2,trippyFragmentShader2)

    const camera = getCamera1(sizes)

    const controls = new OrbitControls(camera, canvas)
    controls.enableDamping = true
    controls.rotateSpeed =1
    controls.enablePan = false
    controls.autoRotate = true
    controls.minDistance = 2.8;
    controls.maxDistance = 3.5;
    controls.autoRotate = false
    // scene.add(trippyMesh)


    const light = getLight1()
    scene.add(light)
    scene.add(camera)

    // Scene 2 (main scene)
    const scene2 = new THREE.Scene()

    const camera2 = getCamera2(sizes)
    const effectComposer =  getEffectComposer(renderer,sizes,scene2,camera2)
    const controls2 = getControls2(camera2,canvas)
    const renderTarget = new THREE.WebGLRenderTarget(512, 512,{antialias: true});
    const plane = getPlaneRoundedCorners(renderTarget)
    const planeTransparent = getPlaneRoundedCornersTransparent()
    scene2.add(light)
    scene2.add(planeTransparent);


    scene2.add(plane);
    const avaxLogo = getAVAXLogo()
    const avaxLogo2 = getAVAXLogo()
    scene2.add(avaxLogo2)
    scene2.add(avaxLogo)
    avaxLogo.position.z = 1.1
    avaxLogo2.position.z = -1.1
    avaxLogo2.rotation.y = Math.PI 


    if(this.props.hasRandomInitialPosition){
      var randomX = Math.ceil(Math.random() * 5) * (Math.round(Math.random()) ? 1 : -1)
      var randomY = Math.ceil(Math.random() * 3) * (Math.round(Math.random()) ? 1 : -1)
      camera2.position.set(randomX, randomY, 17)
    }

    // post processing
    // const glitchPass = new GlitchPass()
    // effectComposer.addPass(glitchPass)
    controls.rotateSpeed =0.8

    this.scene = scene
    this.camera = camera
    this.camera2 = camera2
    this.renderer = renderer

    this.mesh = mesh
    
    this.controls = controls
    this.controls2 = controls2
    this.renderTarget = renderTarget
    this.effectComposer = effectComposer

    // mandatory stuff
    this.mount.appendChild(this.renderer.domElement)
    this.start()
  }

  componentWillUnmount() {
    this.stop()
    this.mount.removeChild(this.renderer.domElement)
  }

  start() {
    if (!this.frameId) {
      this.frameId = requestAnimationFrame(this.animate)
    }
  }

  stop() {
    cancelAnimationFrame(this.frameId)
  }

  animate() {
    const elapsedTime = clock.getElapsedTime()

    this.mesh.material.uniforms.uTime.value =elapsedTime*0.5

    this.controls.update()
    this.controls2.update()
    if(this.props.shouldRotateAutomatically && !this.state.shouldStopAutoRotate){
      this.camera2.position.x = 3 * Math.sin(elapsedTime*1.35 )
    }

    this.renderScene()
    this.frameId = window.requestAnimationFrame(this.animate)
  }

  renderScene() {
    this.renderer.setRenderTarget( this.renderTarget );
    this.renderer.render(this.scene, this.camera, this.renderTarget);
    this.renderer.setRenderTarget( null );
    this.renderer.setClearColor( 0x000000, 0 );

    // Render
    this.effectComposer.render()
  }

  render() {
    return (
      <div
        onMouseDown={()=>this.setState({shouldStopAutoRotate:true})}
        style={{ width: '100%', height: '100%' }}
        ref={(mount) => { this.mount = mount }}
      />
    )
  }
}

export default TrippyElement7;