import React from "react";
import TrippyElement1 from "../components/three/TrippyElement1/TrippyElement1"
import TrippyElement2 from "../components/three/TrippyElement2/TrippyElement2"
import TrippyElement3 from "../components/three/TrippyElement3/TrippyElement3"
import SkullElement from "../components/three/SkullElement/SkullElement"
import TrippyElement4 from "../components/three/TrippyElement4/TrippyElement4"
import TrippyElement5 from "../components/three/TrippyElement5/TrippyElement5"
import TrippyElement6 from "../components/three/TrippyElement6/TrippyElement6"
import TrippyElement7 from "../components/three/TrippyElement7/TrippyElement7"

import WaterElement from "../components/three/WaterElement/WaterElement"

class HomePage extends React.Component {
 

    render() {

      let  ID  = window.location.href.split("token/")[1]
      let element 

      if(ID == 9){
        element =  <TrippyElement7  shouldRotateAutomatically={true} hasRandomInitialPosition={true} />
      }else if(ID == 8){
        element =  <TrippyElement6  shouldRotateAutomatically={true} hasRandomInitialPosition={true} />
      }else if(ID == 7){
        element =  <TrippyElement5  shouldRotateAutomatically={true} hasRandomInitialPosition={true} />
      }else if(ID == 6){
        element =  <TrippyElement4  shouldRotateAutomatically={true} hasRandomInitialPosition={true} />
      }else if(ID == 5){
        element =  <SkullElement  shouldRotateAutomatically={true} hasRandomInitialPosition={true} />
      }else if(ID == 4){
        element =  <TrippyElement3  shouldRotateAutomatically={true} hasRandomInitialPosition={true} />
      }else if(ID == 1){
        element =  <TrippyElement1  shouldRotateAutomatically={true} hasRandomInitialPosition={true} />
      } else if(ID == 2){
        element =  <TrippyElement2  shouldRotateAutomatically={true} hasRandomInitialPosition={true} />
      } else{
        element =  <WaterElement  shouldRotateAutomatically={true} hasRandomInitialPosition={true} />
      }

      return (
        <div style={{height:"100vh"}}>
         
          {element}

        </div>
      );
    }
  }
export default HomePage;